import React from 'react';
import axios from 'axios';

const headersConfig = {
  "Content-Type": "multipart/form-data"
};

class AddPhoto extends React.Component {
  constructor(props) {
	super(props);
	this.myRef = React.createRef();
	this.createAlbumRef = React.createRef();
	this.photoRef = React.createRef();
	this.dateRef = React.createRef();
	this.captionRef = React.createRef();
  }

  componentDidUpdate() {
	  this.myRef.current.scrollTo(0, 0);
  }

  handleSubmit = () => {
	const helperStatus = this.createAlbumRef.current;

	var bodyFormData = new FormData();
	bodyFormData.append('idtoken', this.props.token);
	bodyFormData.append('email', this.props.email);
	bodyFormData.append('albumName', this.createAlbumRef.current.value);
	bodyFormData.append('caption', this.captionRef.current.value);
	bodyFormData.append('date', this.dateRef.current.value);
	bodyFormData.append('image', this.photoRef.current.files[0]);

	/*
    let request = {
        idtoken: this.props.token,
		email: this.props.email,
		albumName: this.createAlbumRef.current.value,
		date: this.dateRef.current.value,
		caption: this.captionRef.current.value,
		picture: this.photoRef.current.files[0]
    };
	*/

	if (helperStatus.classList.contains('valid')) {
		helperStatus.classList.remove('valid');
	}

	if (helperStatus.classList.contains('invalid')) {
		helperStatus.classList.remove('invalid');
	}

	//console.log(bodyFormData.getHeaders());
	/*
	for (var value of bodyFormData.values()) {
		console.log(value);
	}
	*/
	axios.post('/addPhoto', bodyFormData, { headers: headersConfig });
	/*
	axios.post('/addPhoto', request)
		.then(function (response) {
		  //helperStatus.classList.toggle('valid');
		  console.log(response);
	    }).catch(function (error) {
		  //helperStatus.classList.toggle('invalid');
		  console.log(error);
        });
	*/
  }

  handleKeyDown = e => {
    if (e.key !== 'Enter') {
		this.createAlbumRef.current.classList.remove('valid');
		this.createAlbumRef.current.classList.remove('invalid');
    } else {
		this.handleSubmit();
	}
  }


  render() {
	return (
	  <div
	    ref={this.myRef}
        id="modal3"
        className="modal"
		style={{"marginTop": "23%", "paddingTop": "20%", "backgroundColor": "rgb(255, 255, 255, 0.95"}}
      >
        <div className="modal-content" style={{}}>
          <a href="#!" className="modal-close waves-effect" style={{display: "inline-block"}}>
		    Close
          </a>
          <h4>Add Photos</h4>
		  <div className="row">
			<div className="input-field col s12">
			  <input ref={this.createAlbumRef} id="albumNamePhoto" type="text" placeholder="e.g. Memories" onKeyDown={this.handleKeyDown} />
			  <label htmlFor="albumNamePhoto">Album Name</label>
			  <span className="helper-text" data-error="Album exists / is invalid." data-success="Album created!"></span>
			</div>
			<div className="col s12">
			  <form action="#">
			<div className="file-field input-field">
			  <div className="btn">
				<span>File</span>
				<input type="file" ref={this.photoRef}/>
			  </div>
			  <div className="file-path-wrapper">
				<input className="file-path validate" type="text"/>
			  </div>
			</div>
		  </form>
			</div>
			<div className="input-field col s12">
			  <input ref={this.dateRef} id="date" type="text" placeholder="e.g. 12/12/2012" />
			  <label htmlFor="date">Date</label>
			</div>
			<div className="input-field col s12">
			  <input ref={this.captionRef} id="desc" type="text" placeholder="e.g. Walk in snow." />
			  <label htmlFor="desc">Caption</label>
			</div>
		  </div>
		  <a href="#!" onClick={this.handleSubmit} className="waves-effect waves-light btn"><i className="material-icons left">cloud</i>Submit</a>
        </div>
      </div>
	);
  }
}

export default AddPhoto;
